import React from "react"
import "./slider.css"

class Slider extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      pos: 0,
    }
  }
  next() {
    this.slideRight()
  }
  previous() {
    this.slideLeft()
  }
  slideLeft() {
    let { pos } = this.state
    var sliderWidth = document.querySelector("#slider-wrap").clientWidth
    var totalSlides = document.querySelectorAll("ul#slider li").length
    var sliderWidtha = document.querySelector("#slider-wrap ul#slider")
    this.setState(
      {
        pos: --pos,
      },
      () => {
        console.log()
        if (pos == -1) {
          this.setState(
            {
              pos: totalSlides - 1,
            },
            () => {
              sliderWidtha.style.left = -(100 * this.state.pos) + "%"
              this.pagination()
            }
          )
        } else {
          sliderWidtha.style.left = -(100 * this.state.pos) + "%"
          this.pagination()
        }
      }
    )
  }
  slideRight() {
    let { pos } = this.state
    var sliderWidth = document.querySelector("#slider-wrap").clientWidth
    var totalSlides = document.querySelectorAll("ul#slider li").length
    var sliderWidtha = document.querySelector("#slider-wrap ul#slider")
    this.setState(
      {
        pos: ++pos,
      },
      () => {
        if (pos == totalSlides) {
          this.setState(
            {
              pos: 0,
            },
            () => {
              sliderWidtha.style.left = -(100 * this.state.pos) + "%"
              this.pagination()
            }
          )
        } else {
          sliderWidtha.style.left = -(100 * this.state.pos) + "%"
          this.pagination()
        }
      }
    )
  }
  pagination() {
    let { pos } = this.state
    var potalSlides = document.querySelectorAll("#pagination-wrap ul li")
    for (var i = 0; i < potalSlides.length; i++) {
      potalSlides[i].classList.remove("active")
    }
    document.querySelectorAll("#pagination-wrap ul li")[pos].classList.add("active")
  }
  componentDidMount() {
    var totalSlides = document.querySelectorAll("ul#slider li").length
    var sliderWidth = document.querySelector("#slider-wrap").clientWidth
    var sliderWidtha = document.querySelector("#slider-wrap ul#slider")
    sliderWidtha.style.width = `${100 * totalSlides}%`

    for (var i = 0; i < totalSlides; i++) {
      var li = document.createElement("li")
      document.querySelector("#pagination-wrap ul").appendChild(li)
      document.querySelectorAll("ul#slider li")[i].style.width = `${(1 / totalSlides) * 100}%`
    }
    this.pagination()
  }
  render() {
    return (
      <>
        <div id="slider-wrap">
          <ul id="slider">
            <li className="clearfix">
              <div className="sm-col sm-col-12 p2 center">
                <h3>关于我</h3>
              </div>
              <div className="col col-6 sm-col sm-col-6 md-col-6 p2 center">
                <img className="head-img" src={require('../i.png')} />
              </div>
              <div className="col col-6 sm-col sm-col-6 md-col-6 p2">
                <div className="">
                  <h2 className="h2">Sroot</h2>
                  <p className="h4">一名沉迷学习，日渐消瘦的程序员</p>
                </div>
                <div className="pt1">
                  <h2 className="h2">联系方式</h2>
                  <p className="">
                  <a className="contact-link h4" href="mailto:875756931@qq.com" target="_blank">
                    邮箱
                  </a>
                  <a className="contact-link h4" href="https://www.cnblogs.com/sroot" target="_blank">
                    博客
                  </a>
                  <a className="contact-link h4" href="https://weibo.com/u/6505063776" target="_blank">
                    微博
                  </a>
                  </p>
                  
                </div>
              </div>
            </li>
            <li className="clearfix">
              <div className="sm-col sm-col-12 p2 center">
                <h3>觉得不错的话，打个赏呗</h3>
              </div>
              <div className="col col-6 sm-col sm-col-6 md-col-6 center">
                <h4>支付宝</h4>
                <img className="qrcode-tip" src={require('../qrcode-alipay.png')} />
              </div>
              <div className="col col-6 sm-col sm-col-6 md-col-6 center">
                <h4>微信支付</h4>
                <img className="qrcode-tip" src={require('../qrcode-wechat.png')} />
              </div>
            </li>
          </ul>
          <div className="btns" id="next" onClick={this.next.bind(this)}>
            &gt;
          </div>
          <div className="btns" id="previous" onClick={this.previous.bind(this)}>
            &lt;
          </div>
          <div id="pagination-wrap">
            <ul></ul>
          </div>
        </div>
      </>
    )
  }
}
export default Slider
