import React from "react"
import "./dialog.css"

class Dialog extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  close() {
    this.props.onCancel()
  }

  render() {
    return (
      <div className={this.props.isShow ? "dialog-box show" : "dialog-box hide"}>
        <div className="dialog-mask" onClick={this.close.bind(this)}></div>
        <div className="dialog-content">{this.props.content}</div>
      </div>
    )
  }
}

export default Dialog
